function SidenavItem(params: { name: string, iconPath: string, isActive: boolean }) {
  if (params.isActive) {
    return (
      <div className={css(sidenavItemsStyle.sidenavItem)}>
        <img className={css(sidenavItemsStyle.sidenavItemIconActive)} alt={params.name} src={params.iconPath} />
        <div className={css(sidenavItemsStyle.sidenavItemTextActive, styles.centerContainer)}>
          {params.name}
        </div>
      </div>
    )
  }

  return (
    <div className={css(sidenavItemsStyle.sidenavItem)}>
      <img className={css(sidenavItemsStyle.sidenavItemIcon)} alt={params.name} src={params.iconPath} />
      <div className={css(sidenavItemsStyle.sidenavItemText, styles.centerContainer)}>
        {params.name}
      </div>
    </div>
  )
}

