const trendStyle = StyleSheet.create({
  trend: {
  },
  name: {
    font: 'normal 14px "Montserrat", Helvetica, Arial, serif',
  },
  tag: {
    font: 'bold 20px "Montserrat", Helvetica, Arial, serif',
  },
  likes: {
    font: 'normal 12px "Montserrat", Helvetica, Arial, serif',
  },
})


function Trend(params: { name: string, tag: string, likes: number }) {
  const { name, tag, likes } = params
  let likesText
  if (likes < 1000000) {
    likesText = `${likes / 1000}K`
  } else if (likes < 1000000000) {
    likesText = `${likes / 1000000}M`
  } else if (likes < 1000000000000) {
    likesText = `${likes / 1000000000}B`
  } else {
    likesText = likes
  }

  return (
    <div className={css(trendStyle.trend)}>
      <div className={css(trendStyle.name)}>{name}</div>
      <div className={css(trendStyle.tag)}>#{tag}</div>
      <div className={css(trendStyle.likes)}>{likesText} likes</div>
    </div>
  )
}



type UserCard = {
  Name: string,
  Tag: string,
  AvatarSrc?: string,
  IsVerified: boolean,
}

function UserCardView(params: { userCard: UserCard }) {
  const { userCard } = params

  // let avatar
  // if (userCard.AvatarSrc) {
  //   avatar = (
  //     <img className={css(styles.avatar)} alt="avatar" src={userCard.AvatarSrc} />
  //   )
  // } else {
  //   avatar = (
  //     <div className={css(styles.avatar, styles.centerContainer)}>
  //       <img className={css(styles.undefinedAvatar)} alt="avatar" src={UserAvatarIcon} />
  //     </div>
  //   )
  // }

  return (
    <div className={css(styles.container2)}>
      {userCard.AvatarSrc ? (
        <img className={css(styles.avatar)} alt="avatar" src={userCard.AvatarSrc} />
      ) : (
        <div className={css(styles.avatar, styles.centerContainer)}>
          <img className={css(styles.undefinedAvatar)} alt="avatar" src={UserAvatarIcon} />
        </div>
      )}
      <div className={css(styles.userContent, styles.centerContainer)}>
        <div>
          <div className={css(styles.userName)}>{userCard.Name}</div>
          <div className={css(styles.userMention)}>@{userCard.Tag}</div>
        </div>
      </div>
    </div>
  )
}

